@import url('https://fonts.googleapis.com/css2?family=Istok+Web&family=Jost:ital,wght@1,300&family=Saira+Condensed:wght@100&display=swap');

/* Technical skills */
 .tech-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 100px 0;
}
.tech-title {
    text-align: center;
    font-size: 50px;
    font-weight: 700;
    font-family: 'Istok Web', sans-serif;
  }
.lists-skill {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 50px 0;
    margin-top: 70px;
    margin-bottom: 70px;
}
.tech-name {
    margin-top: 20px;
    color: black;
    font-family: 'Istok Web', sans-serif;
    font-size: 20px;
    font-weight: 300;
}
.card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.svg {
    width: 35%;
}

.figma-svg {
    width: 35%;
    border-radius: 10px;
}

    /* responsive Mobile screen */
    @media screen and (max-width: 375px) {
        .tech-container {
            flex-direction: column;
        }
        .tech-title {
            font-size: 30px;
        }
        .lists-skill {
            grid-template-columns: repeat(2, 1fr) !important;
            padding: 0;
        }
    }
    /* responsive Tablet screen */
    @media screen and (max-width: 768px) {
        .tech-container {
            flex-direction: column;
        }
    }
    /* responsive Tablet screen */
    @media screen and (max-width: 1050px) {
        .tech-container {
            flex-direction: column;
        }
        .tech-title {
            font-size: 40px;
        }
        .lists-skill {
            grid-template-columns: repeat(3, 1fr);
            padding: 0;
        }
    }
