* {
    margin: 0;
}

.big-blogs-container{
    margin-top: 100px;
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-gap: 50px;
}

.blog-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
}


.blogs-card {
    width: 80%;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    -webkit-filter: drop-shadow(5px 5px 5px rgb(133, 133, 133) );
    filter: drop-shadow(5px 5px 5px rgba(148, 148, 148, 0.413));
}

.title {
    font-size: large;
    color: black;
    font-weight: 300;
    font-family: 'Overpass', sans-serif;
}

.navbar-bg {
    background-color: black;
    height: 75px;
}


.blog-title{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
    font-size: 60px;
    font-weight: 700;
    font-family: 'Overpass', sans-serif;
}
